<template>
  <div class="sys-main">

    <div class="col-7">
      <div class="catetime">
        <h3>执法案件分析</h3>
        <dl>
          <dd :class="{'active': dateType==1}" @click="dateType=1">近一月</dd>
          <dd :class="{'active': dateType==2}" @click="dateType=2">近半年</dd>
          <dd :class="{'active': dateType==3}" @click="dateType=3">近一年</dd>
        </dl>
      </div>
    </div>

    <m1 />
    <m2 :dateType="dateType" />
    <m3 :dateType="dateType" />
    <m4 :dateType="dateType" />
    <m5 :dateType="dateType" />
    <m6 :dateType="dateType" />
    <m7 :dateType="dateType" />

  </div>
</template>

<script>
// import M1 from '@/views/lawcase/components/M1'
// import M2 from '@/views/lawcase/components/M2'
// import M3 from '@/views/lawcase/components/M3'
// import M4 from '@/views/lawcase/components/M4'
// import M5 from '@/views/lawcase/components/M5'
// import M6 from '@/views/lawcase/components/M6'
// import M7 from '@/views/lawcase/components/M7'
export default {
  data () {
    return {
      dateType: 1
    }
  },
  components: {
    M1: () => import('./components/M1'),
    M2: () => import('./components/M2'),
    M3: () => import('./components/M3'),
    M4: () => import('./components/M4'),
    M5: () => import('./components/M5'),
    M6: () => import('./components/M6'),
    M7: () => import('./components/M7')
  }
}

</script>



